.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Main {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

path.link {
  fill: none;
  stroke: #666;
  stroke-width: 1.5px;
}

text {
  text-anchor: middle;
  font-family: "Helvetica Neue", Helvetica, sans-serif;
  fill: #666;
  font-size: 16px;
}

.active{
   // color: green;
   // background-color: red;
   border-bottom: 3px solid red;
   font-weight: bold;
}

.nav-link{
   
}

.navbar-toggler{
   border-bottom: none;
}
  
.nav-link:hover {
  border-bottom: 3px solid red;
  //font-weight: bold;
}

.navbar{
   padding-left: 5%;
   padding-right: 5%;
   padding-top: 0px;
   padding-bottom: 0px;
   background-color: white;
   box-shadow: 0 0 15px ;   
}

.dropdown-item.active{
   background-color: white;
   color: black;
}

.PageContent{
   padding-top: 50px;
   min-height: 100vh;
}

.Footer{
   background-color: #222222;
   color: white;
   text-align: center;
}

.textarea{
   min-width: 100%;

   min-height: 300px;
   background-color: red;
}

.ellipse {
  fill: cyan;
  stroke: #333;
  stroke-width: 1.5px;
}

.uri {
  fill: cyan;
}

.literal {
  fill: red;
}

.faded {
  opacity: 0.3;
}



.ellipse_highlight {
  fill: yellow;
  stroke: blue;
  stroke-width: 2px;
}

#forcegraph {
  height: 800px;
}